import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

import { configMgr } from "../common/configMgr";
import { GameMain, Sleep } from "../common/GameMain";
import { Message } from "../common/Message";
import { UiMgr } from "../common/uiMgr";
import { sp } from 'cc';

@ccclass('VGirl')
export class V_Girl extends Component {
    @property(Node)
    private stop: Node | null = null;
    @property(Node)
    private play: Node | null = null;
    @property(sp.Skeleton)
    girlSp: sp.Skeleton = null;
    init() {

    }
    onDisable() {

    }
    protected onDestroy(): void {
        this.unscheduleAllCallbacks();
    }
    private count;
    playAction(num) {
        this.count = num;
        this.doAction();
    }
    async doAction() {
        this.stop.active = true;
        this.play.active = false;
        this.girlSp.setAnimation(0, 'animation', false);
        if (this.count > 0) {
            // this.stop.active = !this.stop.active;
            // this.play.active = !this.stop.active;
            this.count--;
            this.scheduleOnce(this.doAction, 0.4);

        } else {
            // this.stop.active = true;
            // this.play.active = !this.stop.active;
            // this.girlSp.setAnimation(0, 'animation', false);
        }
    }
}