import { _decorator, Prefab, Node, Vec2, instantiate, tween } from 'cc';
import { Coin } from "./Coin";

export class coinEffect {
    public static _instance: coinEffect;
    public static get instance(): coinEffect {
        if (!this._instance) {
            this._instance = new coinEffect();
        }
        return this._instance;
    }
    coin: Prefab | null = null;
    parentNode: Node;
//    /**生产金币个数 */
    private num: number = 20;
//    /**金币创建时间区间 */
    private createTime: number = 500;
//    /**金币飞行时间 */
    private flyTime: number = 800;
//    /**贝塞尔曲线y轴浮动 */
    private change: number = 100;
//     /**起始位置浮动 */
     private changePos: number = 10;
    private _timeOutArr: number[] = [];
    create(sPos: Vec2, ePos: Vec2) {
        for (let i = 0; i < this.num; i++) {
            let time:number = this.createTime*Math.random();
            this._timeOutArr.push(
            setTimeout(() => {
                let coin:Coin = instantiate(this.coin).getComponent(Coin);
                let sCPos:Vec2 = this.sCPos(sPos)
                let cPos:Vec2 = this.centerPos(sCPos,ePos);
                coin.setPos(sCPos,cPos,ePos);
                coin.t = 0;
                coin.node.parent = this.parentNode;
                tween(coin).to(this.flyTime / 1000, { t: 1 }, {}).start()
                }, time)
            );
            // console.log("XXXXXXXXXXXXXX："+this._timeOutArr.length)
        }
    }
y
    private sCPos(pos: Vec2): Vec2 {
        let temp: Vec2 = new Vec2();
        temp.x = pos.x-this.changePos+2*this.changePos*Math.random();
        temp.y = pos.y-this.changePos+2*this.changePos*Math.random();
        return temp;
    }
    private centerPos(p1: Vec2, p2: Vec2): Vec2 {
        let pos: Vec2 = new Vec2();
        pos.x = p2.x- this.change + 2 * this.change * Math.random();
        pos.y = (p2.y - p1.y) / 2 + p1.y - this.change + 2 * this.change * Math.random();
        return pos;
    }
}