import { _decorator, Component, Node, Event, Label, EventTouch, instantiate, Vec3, UITransform } from 'cc';
const { ccclass, property } = _decorator;

import { configMgr } from "../common/configMgr";
import { GameMain } from "../common/GameMain";
import { Message } from "../common/Message";
import { C_Player } from "../player/C_Player";
import { D_Player } from "../player/D_Player";
import { C_Tip } from "../tip/C_Tip";
import { e_action } from "../vo/S_OnAction";
import { C_Game } from "./C_Game";
import { Aclip } from '../common/define';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';

@ccclass('VAdd')
export class V_Add extends Component {
    @property(Node)
    private addOne: Node | null = null;  
    @property(Node)
    private addBarBg: Node | null = null;  
    @property(Node)
    private addNode: Node | null = null;  
    @property(Node)
    private reduceNode: Node | null = null;  
    private arr:Node[];
    start() {
        this.addBarBg.on(Node.EventType.TOUCH_START,this.touchStart,this)
        this.addBarBg.on(Node.EventType.TOUCH_MOVE,this.touchMove,this)
        this.addBarBg.on(Node.EventType.TOUCH_END,this.touchEnd,this)

        this.addNode.on(Node.EventType.TOUCH_START,this.addtouchStart,this)
        this.addNode.on(Node.EventType.TOUCH_CANCEL,this.addtouchEnd,this)
        this.addNode.on(Node.EventType.TOUCH_END,this.addtouchEnd,this)

        this.reduceNode.on(Node.EventType.TOUCH_START,this.subtouchStart,this)
        this.reduceNode.on(Node.EventType.TOUCH_CANCEL,this.subtouchEnd,this)
        this.reduceNode.on(Node.EventType.TOUCH_END,this.subtouchEnd,this)
    }
    init(){
        this.addOne.active = false;
        this.arr = [this.addOne];
        for(let i=1;i<20;i++){
            let node = instantiate(this.addOne);
            node.setPosition(0, 18 * i, 0);
            node.parent = this.addOne.parent
            this.arr.push(node);
        }
    }
    private _nowDate:number;
    update(dt) {
        if(this._type!=0){
            let date: Date = new Date();
            this._nowDate = date.getTime();
        }
        if(this._nowDate-this._date>100){
            if(this._type==1){
                this.add();
            }else if(this._type==2){
                this.sub();
            }
            let date: Date = new Date();
            this._date = date.getTime();
        }
    }
    private _touchBn:boolean = false;
    private touchStart(e:EventTouch){
        this._touchBn = true;
    }
    private touchMove(e:EventTouch){
        const touchPos = e.getUILocation();
        let pos = new Vec3();
        this.node.getComponent(UITransform)?.convertToNodeSpaceAR(new Vec3(touchPos.x,touchPos.y), pos);
        if (this.addBarBg && this.addBarBg.getComponent(UITransform)) {
            const addBarBgTransform = this.addBarBg.getComponent(UITransform)!;
            const bgPos = new Vec3();
            this.addBarBg.getWorldPosition(bgPos);
            const localBgPos = new Vec3();
            this.node.getComponent(UITransform)?.convertToNodeSpaceAR(bgPos, localBgPos);
            console.log('pos:', pos.y);
            console.log('localBgPos:', localBgPos.y);
            let aa = Math.floor((pos.y - localBgPos.y) / addBarBgTransform.height * 20);
            console.log('aa:', aa);
            this.count =  aa;
            console.log('count:', this.count);
        }
    }
    private touchEnd(e:EventTouch){
        this._touchBn = false;
    }
    private _type:number;
    private _date=0;
    private addtouchStart(e:EventTouch){
        this._type = 1;
        let date: Date = new Date();
        this._date = date.getTime();
    }
    private addtouchEnd(e:EventTouch){
        if(this._type==1){
            this.add();
        }else if(this._type==2){
            this.sub();
        }
        this._type = 0;
    }
    private subtouchStart(e:EventTouch){
        this._type = 2;
        let date: Date = new Date();
        this._date = date.getTime();
    }
    private subtouchEnd(e:EventTouch){
        if(this._type==1){
            this.add();
        }else if(this._type==2){
            this.sub();
        }
        this._type = 0;
    }
    private get dis():number{
        let play:D_Player = C_Player.instance.gamePlayer;
        let maxCount:number = play.curMoney;
        if(play.curMoney > C_Game.instance.maxBet*C_Player.instance.score)
        {
            maxCount = C_Game.instance.maxBet*C_Player.instance.score;
        }
        let result:number =maxCount-C_Game.instance.min_raise;
        return result;
    }
    private get oneNum():number{
        let num:number = this.dis/20;
        return num;
    }
    private _count:number;
    get count():number{
        return this._count;
    }
    set count(num:number){
        this._count = num;
        if(this._count>20){
            this._count = 20;
        }
        if(this._count<0){
            this._count = 0;
        }
        this.bindBlock();
        this.bindLabel();
    }
   // showBlock(num:number){
   //     let temp = Math.floor(num/this.dis*20)
   //     if(temp>20){
   //         temp = 20;
   //     }
   //     this.count = temp;
   // }
    private bindBlock(){
        for(let i=0;i<20;i++){
            let node = this.arr[i];
            node.active = i<this.count;
        }
    }
    private bindLabel(){
        let num:number = Math.floor(this._count*this.oneNum+C_Game.instance.min_raise);
        // 添加最大金额限制
        if(num>C_Game.instance.maxBet*C_Player.instance.score)
        {
            num = C_Game.instance.maxBet*C_Player.instance.score;
        }
        this.label.string = num +"";
    }
    private _label:Label
    private get label():Label{
        if(!this._label){
            this._label = this.node.getChildByName("label").getComponent(Label);
        }
        return this._label;
    }
    private get lastMoney():number{
        return C_Player.instance.gamePlayer.money-C_Game.instance.game_bet;
    }
    private max(e: EventTouch){
        tgx.AudioMgr.inst.audio.playEffect(Aclip.click,ModuleDef.POKER_DEZHOU);
        C_Game.instance.play(C_Game.instance.cur_seat,e_action.allin,this.lastMoney);
        GameMain.evt.emit(Message.closeAdd)
    }
    private allPool(e: EventTouch){
        tgx.AudioMgr.inst.audio.playEffect(Aclip.click,ModuleDef.POKER_DEZHOU);
        if(C_Game.instance.pot < this.lastMoney){
            C_Game.instance.play(C_Game.instance.cur_seat,e_action.raise,C_Game.instance.pot);
            GameMain.evt.emit(Message.closeAdd)
        }else{
            C_Tip.instance.showTip(LanguageData.getLangByID("dezhou_18"));
        }
    }
    private three(e: EventTouch){
        tgx.AudioMgr.inst.audio.playEffect(Aclip.click,ModuleDef.POKER_DEZHOU);
        let num:number = 3*C_Player.instance.score+C_Game.instance.call;
        if(num < this.lastMoney){
            C_Game.instance.play(C_Game.instance.cur_seat,e_action.raise,num);
            GameMain.evt.emit(Message.closeAdd)
        }else{
            C_Tip.instance.showTip(LanguageData.getLangByID("dezhou_18"));
        }
    }
    private add(){
        tgx.AudioMgr.inst.audio.playEffect(Aclip.click,ModuleDef.POKER_DEZHOU);
        let num = Number(this.label.string)+2;
        if(num>this.lastMoney){
            num = this.lastMoney;
        }
         //最大--250倍大盲注
        if(num>C_Game.instance.maxBet*C_Player.instance.score)
        {
            num = C_Game.instance.maxBet*C_Player.instance.score;
        }
        this.label.string = num+"";
    }
    private sub(){
        tgx.AudioMgr.inst.audio.playEffect(Aclip.click,ModuleDef.POKER_DEZHOU);
        let num = Number(this.label.string)-2;
        if(num<0){
            num = 0;
        }
        this.label.string = num+"";
    }
    private sure(e: EventTouch){
        tgx.AudioMgr.inst.audio.playEffect(Aclip.click,ModuleDef.POKER_DEZHOU);
        if(this.bet>=0){
        if(this.bet<this.lastMoney){
        C_Game.instance.play(C_Game.instance.cur_seat,e_action.raise,this.bet);
        GameMain.evt.emit(Message.closeAdd)
        }else {
        this.max(null);
        }
        }else{
        C_Tip.instance.showTip(LanguageData.getLangByID("dezhou_19"));
        }
    }
    private closeAdd(e: EventTouch){
        tgx.AudioMgr.inst.audio.playEffect(Aclip.click,ModuleDef.POKER_DEZHOU);
        this.node.active = false;
    }
    get bet():number{
        let num:number = Number(this.label.string);
        return num;
    }
}