import { _decorator } from 'cc';
import { GameMain } from "../common/GameMain";
import { Message } from "../common/Message";
import { layerIndex, UiMgr, uiPanel } from "../common/uiMgr";

export class C_Loading{
    public static _instance: C_Loading = null;
    public static get instance():C_Loading{
        if(!this._instance){
            this._instance = new C_Loading();
        }
        return this._instance;
    }
    constructor() {

    }
    private showBn:boolean = false;
    showLoading():void{
        if( this.showBn){
            return;
        }
        UiMgr.showLoadingView();
        this.showBn = true;
        console.log("showLoading");
    } 
    hideLoading(){
        UiMgr.hideLoadingView();
        this.showBn = false;
        console.log("hideLoading");
    }
}