import { _decorator } from 'cc';
import { D_Player } from "./D_Player";
import { UserMgr } from 'db://assets/module_basic/scripts/UserMgr';
export interface I_Player {
    uid: string, gold: number, seat: number, nickname: string, head: string,pic:number,isReady:boolean,seat_type?:number,bet?:number,allin_round?:number,hand_ids?:number[],last_action?:number,round_bet:number
} 

export class C_Player{
    public static _instance: C_Player = null;
    public uid: number = 0;
    private _token: any = 0;
    public set token(num:any){
        // this._token = num;
        console.log("set token:"+this._token)
    }
    public get token():any{
        console.log("get token:"+this._token)
        return this._token;
    }
    public host: string = "";
    public port: number = 0;
    public score:number = 0;
//    /**玩家信息 */
    private _myPlayer: D_Player;
    public players: D_Player[];
    
    public get myPlayer():D_Player{
        if(!this._myPlayer){
            this._myPlayer = new D_Player();
        }
        return this._myPlayer;
    }
    public set myPlayer(value:D_Player){
        this._myPlayer = value;
    }
    public static get instance():C_Player{
        if(!this._instance){
            this._instance = new C_Player();
        }
        return this._instance;
    }
    constructor() {

    }
    init(players:I_Player[]){
        C_Player.instance.myPlayer.uid = UserMgr.inst.uid;
        console.log("游戏中玩家信息初始化")
        C_Player.instance.players = [];
        for(let i:number=0;i<players.length;i++){
            let player = players[i];
            let temp:D_Player = new D_Player();
            temp.init(player.uid,player.nickname,player.gold,player.head,player.pic,player.seat,player.seat_type,player.bet,player.allin_round,player.hand_ids,0,player.isReady,player.last_action,player.round_bet);
            C_Player.instance.players.push(temp);
            console.log("uid:"+player.uid);
            console.log("seat:"+player.seat);
            if(player.uid == UserMgr.inst.uid)
            {
                C_Player.instance.myPlayer = temp;
            }
        }
    }
    playerLeave(id:string)
    {
        
        for(var i:number=0;i<C_Player.instance.players.length;i++)
        {
            if(this.players[i].uid==id)
            {
                this.players.splice(i,1);
                break;
            }
        }
    }
    playerEnter(player:I_Player){
        let temp:D_Player = new D_Player();
        temp.init(player.uid,player.nickname,player.gold,player.head,player.pic,player.seat,player.seat_type,player.bet,player.allin_round,player.hand_ids,0,player.isReady);
        C_Player.instance.players.push(temp);
    }
    getPlayerBySeat(seat:number):D_Player{
        if(this.players){
            for(let i=0;i<this.players.length;i++){
                let player:D_Player = this.players[i];
                if(player.seat==seat){
                    return player;
                }
            }
        }
        return null;
    }
    getPlayerByID(id:string):D_Player{
        if(this.players){
            for(let i=0;i<this.players.length;i++){
                let player:D_Player = this.players[i];
                if(player.uid==id){
                    return player;
                }
            }
        }
        return null;
    }
    clearPlayers(){
        console.log("清空上局游戏中玩家数据")
        C_Player.instance.players = [];
    }
    get gamePlayer():D_Player{
        if(this.players){
            for(let i=0;i<this.players.length;i++){
                let player = this.players[i];
                if(player.isMe){
                    return player;
                }
            }
        }
        return null;
    }
}