import { _decorator, Font, isValid, JsonAsset, Prefab, resources, SpriteAtlas, SpriteFrame } from 'cc';
import { AnimManager, AnimUrl } from "./animManager";
import { GameMain, I_card } from "./GameMain";
import { Message } from "./Message";
import { uiPanel } from "./uiMgr";
import { ResourceMgr } from 'db://assets/core_tgx/base/ResourceMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { sp } from 'cc';

export class configMgr {
    public static ChatTextArr: string[] = [];
    private _cards: I_card[];
    public static _instance: configMgr = null;
    public loaderOver: boolean;
    public static get instance(): configMgr {
        if (!this._instance) {
            this._instance = new configMgr();
        }
        return this._instance;
    }
    constructor() {

    }
    total: number;
    count: number;
    public onloadConfig() {
        this.total = 0;
        this.count = 0;
        this.loaderOver = false;

        this.loadChat();
        this.loadCards();

        for (let key in uiPanel) {
            this.loadPanel(uiPanel[key]);
        }

        this.loadPic("resources/imgs/sBg");
        this.loadPic("resources/imgs/fBg");
        this.loadFont("resources/font/success/successFont");
        this.loadFont("resources/font/fail/failFont");

        this.loadPic("resources/imgs/btnGrey");
        this.loadPic("resources/imgs/btnNo");
        this.loadPic("resources/imgs/btnFlow");
        this.loadPic("resources/imgs/btnPass");
        this.loadPic("resources/imgs/btnAdd");

        this.loadPic("resources/imgs/card/back");

        for (let i = 1; i < 5; i++) {
            for (let j = 2; j < 15; j++) {
                this.loadPic("resources/imgs/card/" + i + "-" + j);
            }
        }

        for (let i = 1; i <= 10; i++) {
            this.loadEffect("type_" + i);
        }

        for (let i = 1; i <= 6; i++) {
            this.loadPic("resources/imgs/head/head_" + i);
        }
        this.loadEffect(AnimUrl.match);
        this.loadEffect(AnimUrl.time);

        this.loadRoomEffect("res/room/chuji/ChuJi");
        this.loadRoomEffect("res/room/zhongji/zhongji");
        this.loadRoomEffect("res/room/gaoji/gaoji");
    }
//    /**加载聊天配置 */
    private loadChat() {
        this.total++;
        this.doLoadChat();
    }
    private doLoadChat() {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_DEZHOU, "resources/json/chat", JsonAsset, (err, asset: JsonAsset) => {
            if (err || !isValid(this)) {
                this.doLoadChat();
                return console.error(err);
            }
            configMgr.ChatTextArr = asset.json.chatList;
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        })
    }

//    /**加载语言配置 */
    private loadCards() {
        this.total++;
        this.doLoadCards();
    }
    private doLoadCards() {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_DEZHOU, "resources/json/cards", JsonAsset, (err, asset: JsonAsset) => {
            if (err) {
                // this.doLoadCards();
                return console.error(err)
            }
            this._cards = asset.json as I_card[];
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        })
    }
//    /**加载图片 */
    private loadPic(url: string) {
        this.total++;
        this.doLoadPic(url);
    }
    private doLoadPic(url: string) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_DEZHOU, url+"/spriteFrame", SpriteFrame, (err, atlas: SpriteFrame) => {
            if (err || !isValid(this)) {
                // this.doLoadPic(url);
                return console.error(err)
            }
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        })
    }
    private loadPanel(url: string) {
        this.total++;
        this.doLoadPanel(url);
    }
    private doLoadPanel(url: string) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_DEZHOU, "resources/uiPanel/" + url, Prefab, (err, atlas: Prefab) => {
            if (err || !isValid(this)) {
                // this.doLoadPanel(url);
                return console.error(err)
            }
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        });
    }
//    /**加载字体 */
    private loadFont(url: string) {
        this.total++;
        this.doLoadFont(url);
    }
    private doLoadFont(url: string) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_DEZHOU, url, Font, (err, atlas: Font) => {
            if (err || !isValid(this)) {
                // this.doLoadFont(url);
                return console.error(err)
            }
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        })
    }
//    /**加载特效 */
    private loadEffect(url: string) {
        this.total++;
        this.doLoadEffect(url);
    }
    private doLoadEffect(url: string) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_DEZHOU, "resources/effect/" + url, SpriteAtlas, (err, atlas: SpriteAtlas) => {
            if (err) {
                // this.doLoadEffect(url);
                return console.error(err)
            }
            this.count++;
            GameMain.evt.emit(Message.loadUpdate);
        });
    }
     /**加载特效 */
        private loadRoomEffect(url: string) {
            this.total++;
            this.doLoadRoomEffect(url);
        }
    
        private doLoadRoomEffect(url: string) {
            ResourceMgr.inst.loadResByModuleBundle(ModuleDef.BASIC, url, sp.SkeletonData, (err, atlas: sp.SkeletonData) => {
                if (err) {
                    return console.error(err)
                }
                this.count++;
                GameMain.evt.emit(Message.loadUpdate);
            });
        }
    get cards(): I_card[] {
        return this._cards;
    }
    getIcardById(id: number): I_card {
        return configMgr.instance.cards[id - 1];
    }
    getIdByIcard(iCard: I_card): number {
        for(let i=0;i<configMgr.instance.cards.length;i++){
            let temp = configMgr.instance.cards[i];
            if(temp.color == iCard.color && temp.score == iCard.score){
                return temp.id;
            }
        }
        return null;
    }
    idToICard(arr: number[]): I_card[] {
        let temp: I_card[] = [];
        for (let i = 0; i < arr.length; i++) {
            let id: number = arr[i];
            let card: I_card = this.getIcardById(id);
            if (!card) {
                console.log("card id error id=" + id);
            }
            temp.push(card);
        }
        return temp;
    }
    iCardToId(arr: I_card[]): number[] {
        let temp: number[] = [];
        for (let i = 0; i < arr.length; i++) {
            let iCard: I_card = arr[i];
            let id: number = this.getIdByIcard(iCard);
            if (!id) {
                console.log("card id error id=" + id);
            }
            temp.push(id);
        }
        return temp;
    }
}