import { _decorator, Component, find, Label } from 'cc';
const { ccclass, property } = _decorator;

import { UiMgr } from "../common/uiMgr";

@ccclass('VTip')
export class V_Tip extends Component {
    
    @property(Label)
    private msg: Label | null = null;
    private showCloseBtn: boolean = true;
    private yesCb: Function = null;
    init(info: string, showCloseBtn = true, yesCb?: () => void,showAways = true) {
        this.msg.string = info;
        this.showCloseBtn = showCloseBtn;
        this.yesCb = yesCb;
        if (!showCloseBtn) {
            find("bg2/btn_close", this.node).active = false;
        }
        if(!this.yesCb){
            find("bg2/btn_yes", this.node).active = false;

        }
        if(!showAways){
        let date: Date = new Date();
        this._time = date.getTime();
        this._cout = 1000;
        }
    }
    private _time:number;
    private _cout:number=0;
    update(dt) {
        if(this._cout>0){
            let date: Date = new Date();
            let timer: number = date.getTime();
            let num: number = Math.floor(timer - this._time);
            if(num > this._cout){
                this._cout = 1000;
                this.onClose();
            }
        }
    }
    private btn_close() {
        if (this.showCloseBtn) {
            this.onClose()
        }
    }
    private btn_yes() {
        this.onClose();
        if (this.yesCb) {
            this.yesCb();
        }
    }
    onClose(){
        UiMgr.closePanel(this)
    }
}
