import { _decorator, Component, Sprite, Label, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('OtherPlayer')
export class otherPlayer extends Component {
    @property(Sprite)
    private man: Sprite | null = null;
    @property(Label)
    private nickname: Label | null = null;
    @property(Label)
    private gold: Label | null = null;
    @property(Sprite)
    private farm: Sprite | null = null;   
    @property(Node)
    private cards: Node | null = null;
//    /**座位号 */
    private _seat: number;
    public get seat():number{
        return this._seat;
    }
    public set seat(num:number){
        this._seat = num;
    }
    onLoad() {

    }
    start() {



    }
    init() {

    }
}
