import { _decorator } from 'cc';
import { GameMain, Sleep } from "../common/GameMain";
import { layerIndex, UiMgr, uiPanel } from "../common/uiMgr";
import { V_Tip } from "./V_Tip";

export class C_Tip{
    public static _instance: C_Tip = null;
    public static get instance():C_Tip{
        if(!this._instance){
            this._instance = new C_Tip();

        }
        return this._instance;
    }
    constructor() {

    }
    public showTip(info: string, showAways = false ,showCloseBtn = false, yesCb?: () => void){
        console.log("showTip");
        UiMgr.showTipsView(uiPanel.TIP,(node) => {
            node.getComponent(V_Tip).init(info, showCloseBtn, yesCb,showAways);
        });
    }
    public async hideTip(){
        UiMgr.closeLayerPanel(layerIndex.tips);
        console.log("hideTip");
    } 
}