import { _decorator, Component, Node, Sprite, Label, tween, resources, SpriteFrame, isValid, Font, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

import { GameMain } from "../common/GameMain";
import { Message } from "../common/Message";
import { UiMgr, uiPanel } from "../common/uiMgr";
import { C_Player } from "../player/C_Player";
import { C_Game } from "./C_Game";
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { Aclip } from '../common/define';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { ResourceMgr } from 'db://assets/core_tgx/base/ResourceMgr';
import { UserMgr } from 'db://assets/module_basic/scripts/UserMgr';
import { director } from 'cc';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';
import { Color } from 'cc';

@ccclass('VGameOver')
export class V_GameOver extends Component {
    @property(Node)
    private itemParent: Node | null = null;
    @property(Sprite)
    private bg: Sprite | null = null;
    @property(Label)
    private txtGold: Label | null = null;
    @property(Label)
    private txtName: Label | null = null;
    @property(Label)
    titleLabel: Label | null = null;
    onLoad() {
        this.init();
        // this.itemParent.setScale(new Vec3(0, 0));
    }
    
    init() {
        let goldStr:number = 0;
        let arr = C_Game.instance.gameOverPlayers;
        for(let i=0;i<arr.length;i++){
            if(arr[i].uid == UserMgr.inst.uid){
                goldStr = Math.floor(arr[i].gold*100)/100;
            }
        }
        let url:string;
        let fontUrl:string;
        if(goldStr>0){
            url = "resources/imgs/sBg/spriteFrame";
            AudioMgr.inst.playOneShot(Aclip.win,1,ModuleDef.POKER_DEZHOU);
            this.txtGold.string = "+"+Math.floor(goldStr*100)/100;
            this.titleLabel.string = 'Victory';
            this.titleLabel.color = new Color().fromHEX("#183365");
            this.titleLabel.outlineColor = new Color().fromHEX("#FF9C00");
            // fontUrl = "resources/font/success/successFont";
            // ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_DEZHOU,fontUrl, Font, (err, font: Font) => {
            //     if (err || !isValid(this)) {
            //         return;
            //     }
            //     // this.txtGold.font = font;
            //     this.txtGold.string = "+"+Math.floor(goldStr*100)/100;
            // })
        }else{
            url = "resources/imgs/fBg/spriteFrame";
            AudioMgr.inst.playOneShot(Aclip.lose,1,ModuleDef.POKER_DEZHOU);
            this.txtGold.string = ""+Math.floor(goldStr*100)/100;
            this.titleLabel.string = 'Fail';
            this.titleLabel.color = new Color().fromHEX("#3e3e3e");
            this.titleLabel.outlineColor = new Color().fromHEX("#a2a2a2");
            // fontUrl = "resources/font/fail/failFont";
            // ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_DEZHOU,fontUrl, Font, (err, font: Font) => {
            //     if (err || !isValid(this)) {
            //         return;
            //     }
            //     // this.txtGold.font = font;
            //     this.txtGold.string = ""+Math.floor(goldStr*100)/100;
            // })
        }
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_DEZHOU,url, SpriteFrame, (err, img: SpriteFrame) => {
            if (err || !isValid(this)) {
                return;
            }
            this.bg.spriteFrame = img;
        })
        this.txtName.string = C_Player.instance.myPlayer.nickname;
    }
    onClose(){
        // var currentScale = this.itemParent.getScale();
        // if(currentScale.x == 0){
        //     tween(this.itemParent).to(0.3, { scale: new Vec3(1,1) }, { easing: "sineInOut" }).call(() => {
        // }).start()
        // }else if(currentScale.x == 1){
        //     tween(this.itemParent).to(0.3, { scale: new Vec3(0,0)}, { easing: "sineInOut" }).call(() => {
        // }).start()
        // }
        UiMgr.closePanel(this);
    }
    onOut(){
       // C_Login.instance.getInfo();
       // UiMgr.closePanel(this);
       // UiMgr.showMainView(uiPanel.LOGIN);
        C_Game.instance.leave();
    }
    async onRestart(){
        
        //重新匹配
        let matchGameAgainRet = await SubGameMgr.inst.MatchGameAgain(SubGameDef.POKER_DEZHOU,SubGameMgr.gameMgr.subType);
        if(matchGameAgainRet.isSucc)
        {
            UiMgr.closePanel(this);
        }
    }
}