import { _decorator, Component, Label, Sprite, Node, EditBox, Event } from 'cc';
const { ccclass, property } = _decorator;

import { UiMgr, uiPanel } from "../common/uiMgr";
import { C_Game, e_scene } from "../game/C_Game";
import { EventTouch } from 'cc';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';

@ccclass('VLogin')
export class V_Login extends Component {
    @property(Node)
    content: Node | null = null
    @property(Node)
    pre: Node | null = null
    @property(Node)
    level: Node | null = null
    onLoad() {
        C_Game.instance.sceneId = e_scene.Login;
  
    }
    start()
    {
        
    }
    private onNormal(e: EventTouch) {
        //进入房间
        SubGameMgr.inst.MatchRoom(SubGameDef.POKER_DEZHOU,true);
    }
    private onMatch(e: EventTouch) {
        return;
    }
 
    private onRule(e: EventTouch) {
        UiMgr.showPopupView(uiPanel.RULE);
    }
}