import { _decorator, Component, Label, resources, Sprite, SpriteFrame, Texture2D } from 'cc';
const { ccclass, property } = _decorator;

import { configMgr } from "../common/configMgr";
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';

@ccclass('LevelItem')
export default class levelItem extends Component {
    @property(Label)
    difen: Label | null = null
    @property(Label)
    renshu: Label | null = null
    @property(Sprite)
    lvicon: Sprite | null = null
    @property(Sprite)
    lv: Sprite | null = null
    @property(Label)
    tagr: Label | null = null
    @property(Label)
    takeMoney: Label | null = null
    score: number = 0
    @property(Label)
    bet: Label | null = null
    async init(index, child: { lowScore: number, onLineNum: number, tagr: number[],takeMoney:number}) {
        this.score = Number(child.lowScore)

        let icon = await this.getItemSprite("level_" + index);
        this.lvicon.spriteFrame = icon;
        let lv = await this.getItemSprite("level_bg_" + index);
        this.lv.spriteFrame = lv;
       // if (index == 0) {
       //     this.difen.node.color = new cc.Color().fromHEX("#75D5B4");
       // } else if (index == 1) {
       //     this.difen.node.color = new cc.Color().fromHEX("#A8D3F3");
       // } else if (index == 2) {
       //     this.difen.node.color = new cc.Color().fromHEX("#EAA4F0");
       // } else if (index == 3) {
       //     this.difen.node.color = new cc.Color().fromHEX("#FFA29B");
       // }
       // if (this.score) {
       //     this.difen.string = LanguageData.getLangByID(7) + this.score;
       // } else {
        this.difen.string = "";
       // }
        this.renshu.string = "" + child.onLineNum;
        if(!child.tagr[1]){
        this.tagr.string = LanguageData.getLangByID("dezhou_20") + child.tagr[0];
        }else{
        this.tagr.string = LanguageData.getLangByID("dezhou_20") + child.tagr[0] + "~" +child.tagr[1];
        }
        this.takeMoney.string = LanguageData.getLangByID("dezhou_22") + child.takeMoney;
        this.bet.string = LanguageData.getLangByID("dezhou_23")+":"+child.lowScore/2 +"/"+child.lowScore;
    }
    getItemSprite(url: string): Promise<SpriteFrame> {
        return new Promise((resolve, reject) => {
        resources.load("imgs/level/" + url, (err, asset: Texture2D) => {
            if (err) {
                console.error(err)
                return
            }
            let spriteFrame = new SpriteFrame();
            spriteFrame.texture = asset;
            resolve(spriteFrame)
        })
        })
    }
}