import { _decorator, Component, Vec2, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

import { Bezier } from "../Utils/Bezier";

@ccclass('Coin')
export class Coin  extends Component {
    private _p0:Vec2;
    private _p1:Vec2;
    private _p2:Vec2;
    private _playBn:boolean;
    public setPos(p0:Vec2,p1:Vec2,p2:Vec2){
        this._p0 = p0;
        this._p1 = p1;
        this._p2 = p2;
        this._playBn = false;
    }
    private _t:number;
    public get t():number{
        return this._t;
    }
    public set t(t:number){
        this._t = t;
        let pos:Vec2 = Bezier.twoBezier(this._p0,this._p1,this._p2,t);
        this.node.setPosition(new Vec3(pos.x,pos.y));
        if(t==1){
            this.node.parent = null;
        }
    }
}