import { _decorator, Component, instantiate, isValid, Node, Prefab, resources, Sprite, SpriteFrame } from 'cc';
const { ccclass, property } = _decorator;

import { GameMain } from "./GameMain";
import { Message } from "./Message";
import { ResourceMgr } from 'db://assets/core_tgx/base/ResourceMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
// /** 值越大层级越高 */
export const enum layerIndex {
//     /**主场景层 */
    main = 0,
//     /**弹窗层 */
    popup = 1,
//     /**提示层 */
    tips = 2,
//     /**加载页面层 */
    loading = 3,
}
export enum uiPanel {
//     /**登录界面 */
    LOGIN = "login/V_Login",
//     /**提示界面 */
    TIP = "tip/V_Tip",
//     /**设置界面 */
    SETTING = "setting/V_Setting",
//     /**规则界面 */
    RULE = "rule/V_Rule",
//     /**游戏界面 */
    GAME = "game/V_Game",
//     /**Loading界面 */
    LOADING = "loading/V_Loading",
//     /**结算界面 */
    GAMEOVER = "game/V_GameOver",
}

@ccclass('UiMgr')
export class UiMgr extends Component {
    private static node: Node | null = null;
    onLoad() {
        UiMgr.node = this.node;
    }
    public static loadPic(sp: Sprite, url: string) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_DEZHOU,"resources/"+url+"/spriteFrame", SpriteFrame, (err, img: SpriteFrame) => {
            if (err || !isValid(this)) {
                return;
            }
            sp.spriteFrame = img;
        })
    }
//    // 加载界面
    private static showPanel(url: string, zIndex: number, cb?: (node: Node) => void) {
        let parent = this.parentNode(zIndex)
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.POKER_DEZHOU, "resources/uiPanel/" + url, Prefab, (err, res: Prefab) => {
            if (err) {
                console.log(url);
                console.error(err);
                return cb && cb(null);
            }
            let node = instantiate(res);
            parent.addChild(node)
            if (parent.children.length > 1) {
                parent.children[0].removeFromParent();
            }
            cb && cb(node);
            if (zIndex == layerIndex.loading) {
            GameMain.evt.emit(Message.updateLoading);
            }
        });
    }
    private static hidePanel(zIndex: number) {
        let parent = this.parentNode(zIndex)
        parent.removeAllChildren();
    }
//    // 关闭界面
    public static closePanel(_this: any, cb?: (node: Node) => void) {
        if (_this.node) {
        _this.node.removeFromParent()
        _this.node.destroy()
        }
    }
//    /**清空图层 */
    public static closeLayerPanel(zIndex: number, cb?: (node: Node) => void) {
        let parent = this.parentNode(zIndex)
        parent.removeAllChildren();
    }
//    // Main场景
    static currentMainView: string;
    public static showMainView(url: string, cb?: (node: Node) => void) {
        UiMgr.currentMainView = url;
        this.showPanel(url, layerIndex.main, cb);
    }
    public static hideMainView(url: string, cb?: (node: Node) => void) {
        this.showPanel(url, layerIndex.main, cb);
    }
//    // 弹窗场景
    public static showPopupView(url: string, cb?: (node: Node) => void) {
        this.showPanel(url, layerIndex.popup, cb);
    }
//    // 关闭弹窗场景
    public static hidePopupView() {
        this.hidePanel(layerIndex.popup);
    }
//    // Tip场景
    public static showTipsView(url: string, cb?: (node: Node) => void) {
        this.showPanel(url, layerIndex.tips, cb);
    }
//    // Loading场景
    public static showLoadingView() {
        let parent = this.parentNode(layerIndex.loading)
        parent.active = true;
    }
    public static hideLoadingView() {
        let parent = this.parentNode(layerIndex.loading)
        parent.active = false;
    }
    public static parentNode(zIndex: number) {
        let parent: Node = null
        switch (zIndex) {
            case layerIndex.main:
                parent = UiMgr.node.getChildByName("mainlayer")
                break;
            case layerIndex.popup:
                parent = UiMgr.node.getChildByName("popuplayer")
                break;
            case layerIndex.tips:
                parent = UiMgr.node.getChildByName("tiplayer")
                break;
            case layerIndex.loading:
                parent = UiMgr.node.getChildByName("lodinglayer")
                break;

        }
        return parent
    }
}