import { _decorator, Component, Label, Sprite, Node, Animation } from 'cc';
const { ccclass, property } = _decorator;

import { AnimManager, AnimUrl } from "../common/animManager";
import { configMgr } from "../common/configMgr";
import { e_room_status } from "./C_Game";
import { sp } from 'cc';

@ccclass('VClock')
export class V_Clock extends Component {
    @property(Label)
    private time: Label | null = null;
    @property(Node)
    clock: Node = null;
    @property(sp.Skeleton)
    private clockBg: sp.Skeleton | null = null;

    private _state: Node;
    public get stateNode(): Node {
        if (!this.start) {
            this._state = this.node.getChildByName("state");
        }
        return this._state;
    }
    public setTime(num: number) {
        this.clockBgBn = num > 3;
        this.clockSpBn = num <= 3;
        this.timeBn = num > 0;
        this.time.string = "" + num;
    }
    public set clockBgBn(bn: boolean) {
        this.clock.active = bn;
        this.timeBn = bn;
    }

    public set clockSpBn(bn: boolean) {
        this.clockBg.node.active = bn;
    }
    public set timeBn(bn: boolean) {
        this.time.node.active = bn;
    }
    public set state(num: number) {
        console.log("XXXXXXXXXXXXXXXX" + num)
        let match = this.stateNode.getChildByName("match");
        let start = this.stateNode.getChildByName("start");
        this.stateNode.active = true;
        if (num == e_room_status.Waiting) {
            if (match.active) {
                return;
            }
            match.active = true;
            start.active = false;
            this.showMatch();
        }
        else if (num == e_room_status.Ready) {
            match.active = false;
            start.active = true;
        } else {
            match.active = false;
            start.active = false;
        }
    }
    private async showMatch() {
        let match = this.stateNode.getChildByName("match");
        match.removeAllChildren();
        let node: Node = await AnimManager.createAnimNode(AnimUrl.match, 1, true)
        let effect: Animation = node.getComponent(Animation)
        node.parent = match;
        node.active = true;
        effect.play("anim_desk");
    }
    public hideState(): void {
        this.stateNode.active = false;
    }
}