import { _decorator, Component, Node } from 'cc';
import { GameMgr } from 'db://assets/module_basic/scripts/GameMgr';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { ResourceMgr } from '../core_tgx/base/ResourceMgr';
import { Prefab } from 'cc';
import { PokerManager } from '../module_basic/scripts/poker/PokerManager';
import { AnimManager } from './scripts/common/animManager';
import { configMgr } from './scripts/common/configMgr';
import { C_Main } from './scripts/main/C_Main';
import { GameMain } from './scripts/common/GameMain';
import { Message } from './scripts/common/Message';
const { ccclass, property } = _decorator;

@ccclass('DezhouGameMgr')
export class DezhouGameMgr extends GameMgr {
    private static _inst: DezhouGameMgr;
    
    public static get inst(): DezhouGameMgr {
        if (!this._inst) {
            this._inst = new DezhouGameMgr();
        }
        return this._inst;
    }

    constructor() {
        super();
        this._gameType = SubGameDef.POKER_DEZHOU;
        this._entryScene = { name: 'lobby_dezhou', bundle: ModuleDef.POKER_DEZHOU };
        this._gameScene = { name: 'game_dezhou', bundle: ModuleDef.POKER_DEZHOU };
        this._loadingScene = { name: 'subgame_loading', bundle: ModuleDef.BASIC };
        
    }
    override init() {
        PokerManager.inst.init();
    }

    override uinit() {
        PokerManager.inst.uinit();
        AnimManager.clearCache();
    }
     /**
     * 加载资源
     */
    public override async preLoadRes(processFun:Function):Promise<boolean> {
        
        return new Promise((resolve, reject) => {
            function loadUpdate(){
                let total:number = configMgr.instance.total;
                let count:number = configMgr.instance.count;
        
                if(count>=total){
                    processFun(1);
                    resolve(true);
                    GameMain.evt.off(Message.loadUpdate,loadUpdate,this);
                    return;
                }
                processFun(count/total);
            }
            GameMain.evt.on(Message.loadUpdate,loadUpdate,this);
            configMgr.instance.onloadConfig();
            C_Main.instance.init();
        });
    }
    // 进入游戏
    public override async enterGame() {
        SubGameMgr.inst.MatchRoom(this.gameType, true);
    }
}
SubGameMgr.inst.registerGameMgr(DezhouGameMgr.inst);


