import { _decorator } from 'cc';
import { C_Game } from "../game/C_Game";
import { C_Player } from "./C_Player";

export class D_Player {
//    /**uid */
    public uid: string;
//    /**昵称 */
    public nickname: string;
//    /**金币 */
    public money: number;
//    /**代金卷 */
    public voucher: number;
//    /**头像 */
    public head: string;
//    /**座位号 */
    public seat: number;
//    /**座位类型 0普通座位 1庄家 2小盲 3大盲 4弃牌 5allin*/
    public seatType: number;
//    /**第几回合allin */
    public allin_round: number;
//    /**allin 回合结束展示手牌 */
    public hand_ids: number[];
//    /**下注 */
    public bet: number;
//    /**当前金币扣除下注 */
    public curMoney: number;
//    /**入场金额 */
    public initGold: number;
    /**是否准备 */
    public isReady: boolean;
    /**当前回合最后操作 */
    public last_action: number;
    /**本回合下的钱 */
    public round_bet:number;

    game_bet: number;
    private _pic: number;
//    /**已进入亲友场桌号 */
    private _deskIndex: number;
    public get deskIndex(): number {
        return Number(this._deskIndex);
    }
    public set deskIndex(a: number) {
        this._deskIndex = a;
    }
    public get pic(): number {
        return Number(this._pic);
    }
    public set pic(num: number) {
        this._pic = num;
    }
    constructor() {

    }
    init(uid: string, nickname: string, money: number, head: string, pic: number, seat: number = 0, seatType: number = -1, bet = 0, allin_round = 0, hand_ids: number[] = [],deskIndex:number=0,isReady:boolean = false,last_action:number=0,round_bet:number = 0): void {
        this.uid = uid;
        this.nickname = nickname;
        this.money = money;
        this.initGold = money;
        this.curMoney = this.money;
        this.head = head;
        this.pic = pic;
        this.seat = seat;
        this.seatType = seatType;
        this.bet = bet;
        this.game_bet = 0;
        this.allin_round = allin_round;
        this.hand_ids = hand_ids;
        this.deskIndex = deskIndex;
        this.isReady = isReady;
        this.last_action = last_action;
        this.round_bet = round_bet;
    }
//    /**是否是玩家本人 */
    public get isMe(): boolean {
        return C_Player.instance.myPlayer.uid == this.uid;
    }
}