import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

import { UiMgr } from "../common/uiMgr";
import { ExtendButton } from 'db://assets/core_tgx/base/ExtendButton';
import { Vec3 } from 'cc';
import { tween } from 'cc';

@ccclass('VRule')
export class V_Rule extends Component {
    @property(Node)
    toogleIcon: Node = null;
    @property(Node)
    desc1: Node = null;
    @property(Node)
    desc2: Node = null;

    private onToggle(e) {
        const bt = e.target.getComponent(ExtendButton);
        let pos = new Vec3(-30, 0, 0);
        this.desc1.active = !bt.isChecked;
        this.desc2.active = bt.isChecked;
        if (bt.isChecked) {
            pos = new Vec3(30, 0, 0);
        }

        tween(this.toogleIcon).to(0.2, { position: pos }).start();
    }

    private btn_close() {
        UiMgr.closePanel(this)
    }
}

