    update(dt) {
        if (this._doStartCountdown) {
            this.doStartCountdown();
        }
        if (this._doFarmCountdown) {
            this.doFarmCountdown();
        }
        if (!this.isStart) return;
        this.nowTime += dt * 1;
        if (!this.preTime) {
            this.preTime = this.nowTime;
            return;
        }
        if ((this.nowTime - this.preTime) >= this.timedist) {
            this.preTime = this.nowTime
            this.updataHistory()
        }
    }
