    //发牌
    private playFlyCards()
    {
        this.cardPool.removeAllChildren();
        let renShu = PokerManager.inst.roomData.userList.length;
        for (let i = 0; i < 2 * renShu; i++) {
            let card = this.cardClone;
            card.setParent(this.cardPool);
            card.setPosition(0, 0);
            card.active = true;
        }
        //分牌
        let i = this.cardPool.children.length - 1;
        let intervalId = setInterval(() => {
            let card = this.cardPool.children[i];
            let vSeat:V_Seat = this.seatArr[i % renShu]
            let toNode = this.seatArr[i % renShu].cards ;
            let isSelf = vSeat.isMe;

            let newVec2 = toNode.getWorldPosition();
            let vec2 = this.cardPool.getComponent(UITransform).convertToNodeSpaceAR(newVec2);
            let scale = isSelf ? 1 : 0.4;
            tween(card)
            .delay(0.05)
            .to(0.3, { position: vec2, scale: new Vec3(scale, scale, scale) })
            .call(() => {
                
                    card.active = false;
                })
            .start();
            i--;
            if (i < 0) {
                clearInterval(intervalId);
                //发牌结束
                this.onPlayFlyCardsEnd();
            }
        },10);
    }
    private onPlayFlyCardsEnd() {
        for (let i = 0; i < this.seatArr.length; i++) {
            let vSeat: V_Seat = this.seatArr[i];
            if (vSeat.isMe) {
                vSeat.setCards(C_Game.instance.dealCards, true);
                vSeat.setCardType(C_Game.instance.cardType);
            } else {
                if (vSeat.node.active) {
                    vSeat.showCardsBack = true;
                    vSeat.setCardType(0);
                }
            }
        }
    }

    private comCardsArr: number[];
    private bindComCards(arr: I_card[]) {
        console.log("绑定公共牌:" + JSON.stringify(arr))
        for (let i = 0; i < arr.length; i++) {
            let iCard: I_card = arr[i];
            for (let j = 0; j < 5; j++) {
                let card: Sprite = this.getComCard(j);
                if (card.node.active == false) {
                    let url;
                    if (iCard) {
                        let color = iCard.color;
                        let score = iCard.score;
                        if (score === e_card_score.little_king || score === e_card_score.big_king) {
                            url = "imgs/card/" + score;
                        } else {
                            url = "imgs/card/" + color + "-" + score;
                        }
                    } else {
                        url = "imgs/card/back";
                    }
                    UiMgr.loadPic(card, url);
                    card.node.active = true;
                    break;
                }
            }
        }
    }
    private bindEndComCards(arr: I_card[]) {
        console.log("绑定公共牌:" + JSON.stringify(arr))
        for (let i = 0; i < 5; i++) {
        let card: Sprite = this.getComCard(i);
        let iCard
        if (i < arr.length) {
            iCard = arr[i]
        }
        if (iCard) {
            let url: string;
            if (iCard) {
                let color = iCard.color;
                let score = iCard.score;
                if (score === e_card_score.little_king || score === e_card_score.big_king) {
                    url = "imgs/card/" + score;
                } else {
                    url = "imgs/card/" + color + "-" + score;
                }
            } else {
                url = "imgs/card/back";
            }
            UiMgr.loadPic(card, url);
            card.node.active = true;
            } else {
                card.node.active = false;
            }
        }
    }
    private hideComCards(arr: number[]) {
        if (arr.length != 5) {
        return;
        }
       // console.log("XXXXXXXXXXXXXXXXX:"+JSON.stringify(this.comCardsArr))
        for (let i = 0; i < 5; i++) {
            let cardId = this.comCardsArr[i];
            let index = arr.indexOf(cardId);
            let grey: Sprite = this.getComCardGrey(i);
            grey.node.active = index == -1;
            // console.log("XXXXXXXXXXXXXXXXX:"+index)
        }
    }
