    /**选择大小盲 */
    private doPlayBigSmallEffect(msg:MsgGameStart) {
        for (let i: number = 0; i < this.seatArr.length; i++)
        {
            this.seatArr[i].playChooseEffect();
            if(this.seatArr[i].seat!=msg.bb_seat && this.seatArr[i].seat!=msg.sb_seat)
            {
                //不是大小盲的话 1秒消失
                this.scheduleOnce(() => {
                    this.seatArr[i].hideChoose();
                },1);
            }
            else
            {
                //大小盲 2秒消失
                this.scheduleOnce(() => {
                    this.seatArr[i].hideChoose();
                },2);
            }
        }

        this.scheduleOnce(() => {
            let vSeat_bb: V_Seat = this.getVSeat(msg.bb_seat);
            vSeat_bb.isFrame = true;
            vSeat_bb.isBb = true;
            vSeat_bb.setRecord(C_Game.instance.bbBet, 1);
            vSeat_bb.setGold(vSeat_bb.player.money - C_Game.instance.bbBet);

            let vSeat_sb: V_Seat = this.getVSeat(msg.sb_seat);
            vSeat_sb.isFrame = true;
            vSeat_sb.isSb = true;
            vSeat_sb.setRecord(C_Game.instance.sbBet, 1);
            vSeat_sb.setGold(vSeat_sb.player.money - C_Game.instance.sbBet);
        },2);
    }

